import service from '../../service.js';
import {
	mapState,
	mapMutations
} from 'vuex'
import mInput from '../../components/m-input.vue'

export default {
	components: {
		mInput
	},
	data() {
		return {
			nickName: null,
			avatarUrl: null,
		}
	},
	computed: mapState(['forcedLogin']),
	methods: {
		...mapMutations(['login']),
		wxGetUserInfo() {
			uni.getUserInfo({
				provider: 'weixin',
				success: function(infoRes) {
					let nickName = infoRes.userInfo.nickName; //昵称
					let avatarUrl = infoRes.userInfo.avatarUrl; //头像
				},
				fail(res) {}
			});
		},
		Autho() {
			let _this = this;
			uni.showLoading({
				title: '登录中...'
			});
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					uni.getUserInfo({
						provider: 'weixin',
						success: function(infoRes) {
							uni.hideLoading();
							let userInfoSet={
								userName:infoRes.userInfo.nickName,
								userAvatar:infoRes.userInfo.avatarUrl
							}
							_this.toMain(userInfoSet);
							
						}
					});
				},
			});
		},
		toMain(userInfoSet) {
			this.login(userInfoSet);
			/**
			 * 强制登录时使用reLaunch方式跳转过来
			 * 返回首页也使用reLaunch方式
			 */
			if (this.forcedLogin) {
				uni.reLaunch({
					url: '../main/main',
				});
			} else {
				uni.navigateBack();
			}

		}
	},
	onLoad() {
		this.Autho();
	}
}
